.products-block {
  &__header {
    font-size: 50px;
    line-height: 65px;
    font-weight: 700;
    color: var(--black, #2d2d2d);

    @include up-mid-size {
      font-size: 40px;
      line-height: 52px;
    }

    @include low-mid-size {
      font-size: 30px;
      line-height: 39px;
    }

    @include min-size {
      font-size: 20px;
      line-height: 26px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 50px 20px;
  
    @include up-mid-size {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, auto);
    }
  
    @include low-mid-size {
      gap: 40px 20px;
    }
  
    @include min-size {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, auto);
      gap: 30px 20px;
    }
  }
}