@charset "UTF-8";
@import url("normalize/normalize.css");
.product__image, .article-link__image, .product-card__image, .promo__card-background {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

/*Шрифты*/
@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-display: swap;
  src: url("../assets/fonts/Lato-400.woff2") format("woff2"), url("../assets/fonts/Lato-400.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 700;
  font-display: swap;
  src: url("../assets/fonts/Lato-700.woff2") format("woff2"), url("../assets/fonts/Lato-700.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-weight: 700;
  font-display: swap;
  src: url("../assets/fonts/OpenSans-700.woff2") format("woff2"), url("../assets/fonts/OpenSans-700.woff") format("woff");
}
/*Стили*/
html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  font-family: "Lato";
  font-weight: 400;
  font-size: 40px;
  line-height: 130%;
  background-color: var(--text-white, #f9f9f9);
}

img, svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  padding: 0;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

iframe {
  border: none;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.container {
  max-width: 1830px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (max-width: 1400px) {
  .container {
    padding: 0 42px;
  }
}
@media (max-width: 900px) {
  .container {
    padding: 0 34px;
  }
}
@media (max-width: 640px) {
  .container {
    padding: 0 20px;
  }
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  column-gap: 30px;
}
@media (max-width: 1400px) {
  .grid-container {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
  }
}
@media (max-width: 900px) {
  .grid-container {
    grid-template-columns: repeat(9, 1fr);
  }
}
@media (max-width: 640px) {
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

.logo {
  font-family: "OpenSans";
  font-weight: 700;
  font-size: 30px;
  line-height: 41px;
  letter-spacing: 0.06em;
}
.logo_color_blue {
  color: var(--dark-blue, #3670c7);
}
@media (max-width: 900px) {
  .logo {
    font-size: 26px;
    line-height: 35px;
  }
}
@media (max-width: 640px) {
  .logo {
    font-size: 18px;
    line-height: 25px;
  }
}

details summary::-webkit-details-marker,
details summary::marker {
  display: none;
  content: "";
}

.header {
  padding-top: 27px;
  padding-bottom: 27px;
}
@media (max-width: 1400px) {
  .header {
    padding-top: 34px;
    padding-bottom: 30px;
  }
}
@media (max-width: 900px) {
  .header {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}
@media (max-width: 640px) {
  .header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.header__container {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  gap: 30px;
  font-weight: 700;
  font-size: 16px;
}
@media (max-width: 1400px) {
  .header__container {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    gap: 34px 24px;
  }
}
@media (max-width: 900px) {
  .header__container {
    grid-template-columns: repeat(9, 1fr);
    gap: 29px 20px;
  }
}
@media (max-width: 640px) {
  .header__container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, auto);
    gap: 20px;
  }
}
.header__logo {
  grid-column: 1/3;
  align-self: center;
}
@media (max-width: 1400px) {
  .header__logo {
    grid-column: 1/3;
    grid-row: 1/2;
    align-self: top;
  }
}
.header__menu-button {
  grid-column: 3/6;
  justify-self: end;
  align-self: center;
  display: flex;
  align-items: center;
  gap: 7px;
  margin: 0 69px 0 0;
  padding: 9px 28px 12px 30px;
  font-weight: 400;
  font-size: 22px;
  line-height: 150%;
  color: var(--text-white, #ffffff);
  background-color: var(--dark-blue, #3670c7);
  border-radius: 10px;
  border: none;
  transition: background-color 0.2s ease;
}
.header__menu-button:hover {
  background-color: #205cb5;
}
@media (max-width: 1700px) {
  .header__menu-button {
    margin: 0 24px 0 0;
  }
}
@media (max-width: 1400px) {
  .header__menu-button {
    grid-column: 1/2;
    grid-row: 2/3;
    justify-self: start;
    margin: 0;
  }
}
@media (max-width: 900px) {
  .header__menu-button {
    padding: 7px 21px 11px 21px;
    font-size: 16px;
    grid-column: 1/3;
    border-radius: 6px;
    gap: 4px;
  }
}
@media (max-width: 640px) {
  .header__menu-button {
    padding: 5px 14px 7px 15px;
    font-size: 12px;
  }
}
.header__button-icon {
  display: inline-flex;
  flex-direction: column;
  gap: 3px;
  width: 24px;
  height: 24px;
  padding: 6px 3px;
  margin: 4px 0 0 0;
}
@media (max-width: 900px) {
  .header__button-icon {
    margin: 0;
  }
}
@media (max-width: 640px) {
  .header__button-icon {
    width: 14px;
    height: 14px;
    padding: 3px 2px;
    gap: 0;
    justify-content: space-between;
  }
}
.header__button-icon span {
  height: 2px;
  background-color: #fff;
}
@media (max-width: 640px) {
  .header__button-icon span {
    height: 1px;
  }
}
.header__button-icon span:first-child {
  width: 18px;
  transition: transform 0.4s ease;
}
@media (max-width: 640px) {
  .header__button-icon span:first-child {
    width: 10px;
  }
}
.header__button-icon span:nth-child(2) {
  width: 12px;
  transition: width 0.2s ease, transform 0.2s ease;
}
@media (max-width: 640px) {
  .header__button-icon span:nth-child(2) {
    width: 7px;
  }
}
.header__button-icon span:last-child {
  width: 6px;
  transition: transform 0.2s ease;
}
@media (max-width: 640px) {
  .header__button-icon span:last-child {
    width: 4px;
  }
}
.header__button-icon_active span:first-child {
  transform: translate(0, 5px) rotate(45deg);
}
@media (max-width: 640px) {
  .header__button-icon_active span:first-child {
    transform: translate(0, 3.5px) rotate(45deg);
  }
}
.header__button-icon_active span:nth-child(2) {
  transform: translate(9px, 0);
  width: 0px;
}
@media (max-width: 640px) {
  .header__button-icon_active span:nth-child(2) {
    transform: translate(5px, 0);
  }
}
.header__button-icon_active span:last-child {
  width: 18px;
  transform: translate(0, -5px) rotate(-45deg);
}
@media (max-width: 640px) {
  .header__button-icon_active span:last-child {
    width: 10px;
    transform: translate(0, -3.5px) rotate(-45deg);
  }
}
.header__search {
  grid-column: 6/13;
  align-self: center;
  display: flex;
  background-color: #efefef;
  border-radius: 30px;
}
@media (max-width: 1400px) {
  .header__search {
    grid-column: 2/5;
    grid-row: 2/3;
  }
}
@media (max-width: 900px) {
  .header__search {
    grid-column: 3/10;
  }
}
@media (max-width: 640px) {
  .header__search {
    grid-column: 1/3;
    grid-row: 3/4;
  }
}
.header__search-input {
  width: 100%;
  padding: 15px 40px 18px 40px;
  line-height: 21px;
  background: transparent;
  border: none;
}
.header__search-input::placeholder {
  color: var(--grey, #8f8f8f);
}
@media (max-width: 900px) {
  .header__search-input {
    padding: 11px 30px 15px 30px;
    font-size: 12px;
    line-height: 16px;
  }
}
@media (max-width: 640px) {
  .header__search-input {
    padding: 9px 16px 11px 16px;
    font-size: 8px;
    line-height: 10px;
  }
}
.header__search-button {
  padding: 9px 35px;
  color: var(--text-white, #ffffff);
  background-color: var(--dark-blue, #3670c7);
  border: none;
  border-radius: 30px;
  transition: background-color 0.2s ease;
}
.header__search-button:hover {
  background-color: #205cb5;
}
@media (max-width: 900px) {
  .header__search-button {
    padding: 4px 28px;
  }
}
@media (max-width: 640px) {
  .header__search-button {
    padding: 4px 16px 2px 16px;
  }
}
@media (max-width: 900px) {
  .header__search-icon {
    width: 30px;
    height: 30px;
  }
}
@media (max-width: 640px) {
  .header__search-icon {
    width: 20px;
    height: 20px;
  }
}
.header__navigation {
  grid-column: 13/17;
}
@media (max-width: 1400px) {
  .header__navigation {
    grid-column: 3/5;
    grid-row: 1/2;
  }
}
@media (max-width: 900px) {
  .header__navigation {
    grid-column: 3/10;
  }
}
@media (max-width: 640px) {
  .header__navigation {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
.header__navigation-list {
  display: flex;
  justify-content: flex-end;
  gap: 70px;
}
@media (max-width: 1700px) {
  .header__navigation-list {
    gap: 40px;
  }
}
@media (max-width: 900px) {
  .header__navigation-list {
    gap: 30px;
  }
}
@media (max-width: 640px) {
  .header__navigation-list {
    gap: 20px;
  }
}
.header__navigation-item {
  flex: 0 1 auto;
}
@media (max-width: 900px) {
  .header__navigation-item {
    flex: 0 0 44px;
  }
}
@media (max-width: 640px) {
  .header__navigation-item {
    flex: 0 0 30px;
    max-height: 30px;
  }
}
.header__navigation-link {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  font-size: inherit;
  line-height: 21px;
  position: relative;
  transition: color 0.2s ease;
}
.header__navigation-link:hover, .header__navigation-link:focus {
  color: var(--dark-blue, #3670c7);
}
.header__navigation-link:active {
  color: #205cb5;
}
@media (max-width: 900px) {
  .header__navigation-link {
    line-height: 16px;
    font-size: 12px;
  }
}
.header__navigation-icon {
  margin: 0 0 4px 0;
}
@media (max-width: 900px) {
  .header__navigation-icon {
    height: 30px;
    margin: 0;
  }
}
@media (max-width: 900px) {
  .header__navigation-text {
    margin: -2px 0 0 0;
  }
}
@media (max-width: 640px) {
  .header__navigation-text {
    display: none;
  }
}
.header__cart-counter {
  position: absolute;
  top: 0;
  right: 13px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 14px;
  height: 14px;
  font-weight: 700;
  font-size: 14px;
  line-height: 130%;
  color: var(--dark-blue, #3670c7);
  background-color: #fff;
  border-radius: 50%;
  z-index: 3;
}
@media (max-width: 900px) {
  .header__cart-counter {
    right: 11px;
  }
}
@media (max-width: 640px) {
  .header__cart-counter {
    right: 0px;
  }
}
.header__menu-overlay {
  display: none;
  overflow: hidden;
  position: absolute;
  top: 100px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 0.4s ease;
}
.header__menu-overlay_active {
  display: block;
}
@media (max-width: 1400px) {
  .header__menu-overlay_active {
    top: 213px;
  }
}
@media (max-width: 900px) {
  .header__menu-overlay_active {
    top: 173px;
  }
}
@media (max-width: 640px) {
  .header__menu-overlay_active {
    top: 175px;
  }
}
.header__menu-box {
  background-color: #fff;
  transition: height 0.4s ease;
  height: 0;
  overflow: hidden;
}
.header__menu {
  padding: 33px 0 92px 0;
}
@media (max-width: 1700px) {
  .header__menu {
    padding: 30px 0 90px 0;
  }
}
@media (max-width: 1400px) {
  .header__menu {
    padding: 30px 0 60px 0;
  }
}
@media (max-width: 900px) {
  .header__menu {
    padding: 25px 0 50px 0;
  }
}
.header__menu-container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 20px;
}
@media (max-width: 900px) {
  .header__menu-container {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 640px) {
  .header__menu-container {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    row-gap: 35px;
  }
}
.header__catalog {
  display: flex;
  flex-direction: column;
  grid-column: 1/4;
}
@media (max-width: 900px) {
  .header__catalog {
    grid-column: 1/2;
  }
}
.header__for-buyers {
  grid-column: 4/6;
}
@media (max-width: 1400px) {
  .header__for-buyers {
    margin: 0 0 0 40px;
  }
}
@media (max-width: 900px) {
  .header__for-buyers {
    grid-column: 2/3;
    margin: 0;
  }
}
@media (max-width: 640px) {
  .header__for-buyers {
    grid-column: 1/2;
  }
}
.header__contacts {
  grid-column: 6/8;
  margin: 0 0 0 -73px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1400px) {
  .header__contacts {
    margin: 0 0 0 10px;
  }
}
@media (max-width: 900px) {
  .header__contacts {
    grid-column: 3/4;
    margin: 0;
  }
}
@media (max-width: 640px) {
  .header__contacts {
    grid-column: 1/2;
  }
}

.navigation-block__title {
  margin: 0 0 40px 0;
  font-size: 32px;
  line-height: 130%;
  font-weight: 700;
}
@media (max-width: 1700px) {
  .navigation-block__title {
    margin: 0 0 30px 0;
  }
}
@media (max-width: 900px) {
  .navigation-block__title {
    margin: 0 0 20px 0;
    font-size: 26px;
  }
}
@media (max-width: 640px) {
  .navigation-block__title {
    margin: 0 0 10px 0;
    font-size: 20px;
  }
}
.navigation-block__title_position_header {
  margin: 0 0 33px 0;
  color: #2d2d2d;
}
@media (max-width: 1400px) {
  .navigation-block__title_position_header {
    font-size: 26px;
  }
}
@media (max-width: 900px) {
  .navigation-block__title_position_header {
    margin: 0 0 20px 0;
    font-size: 20px;
  }
}
@media (max-width: 640px) {
  .navigation-block__title_position_header {
    margin: 0 0 10px 0;
  }
}
.navigation-block__accordeon-btn {
  color: #fff;
  line-height: 130%;
  font-weight: 700;
  background: transparent;
  border: none;
  cursor: default;
}
@media (max-width: 640px) {
  .navigation-block__accordeon-btn {
    display: flex;
    align-items: center;
    gap: 12px;
  }
}
.navigation-block__accordeon-arrow {
  display: none;
}
@media (max-width: 640px) {
  .navigation-block__accordeon-arrow {
    display: inline-block;
    transition: transform 0.4s ease;
  }
  .navigation-block__accordeon-arrow_active {
    transform: rotate(-180deg);
  }
  .navigation-block__accordeon-arrow_position_header {
    display: none;
  }
}
.navigation-block__list {
  max-height: 225px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  column-gap: 42px;
  align-self: start;
}
@media (max-width: 1700px) {
  .navigation-block__list {
    max-height: 170px;
  }
}
@media (max-width: 1400px) {
  .navigation-block__list {
    align-self: auto;
    max-height: 100%;
    flex-wrap: nowrap;
    column-gap: 0px;
    row-gap: 15px;
  }
}
@media (max-width: 900px) {
  .navigation-block__list {
    row-gap: 10px;
  }
}
@media (max-width: 640px) {
  .navigation-block__list {
    row-gap: 8px;
    transition: height 0.4s ease;
    height: 0;
    overflow: hidden;
  }
  .navigation-block__list_active {
    height: auto;
    margin: 0 0 15px 0;
  }
}
.navigation-block__list_position_header {
  max-height: 245px;
  column-gap: 130px;
}
@media (max-width: 1700px) {
  .navigation-block__list_position_header {
    max-height: 172px;
  }
}
@media (max-width: 1400px) {
  .navigation-block__list_position_header {
    max-height: 172px;
    align-self: start;
    flex-wrap: wrap;
    column-gap: 55px;
    row-gap: 0;
  }
}
@media (max-width: 900px) {
  .navigation-block__list_position_header {
    align-self: auto;
    max-height: 100%;
    flex-wrap: nowrap;
    column-gap: 0px;
    row-gap: 10px;
  }
}
@media (max-width: 640px) {
  .navigation-block__list_position_header {
    row-gap: 8px;
    transition: none;
    height: auto;
    overflow: visible;
  }
}
.navigation-block__item {
  display: inline-block;
  font-size: 22px;
  line-height: 150%;
  font-weight: 400;
}
@media (max-width: 1700px) {
  .navigation-block__item {
    font-size: 16px;
  }
}
@media (max-width: 1400px) {
  .navigation-block__item {
    margin: 0;
    font-size: 22px;
  }
}
@media (max-width: 900px) {
  .navigation-block__item {
    font-size: 16px;
  }
}
@media (max-width: 640px) {
  .navigation-block__item {
    font-size: 12px;
  }
}
.navigation-block__item:not(:nth-child(5n)) {
  margin: 0 0 15px 0;
}
@media (max-width: 1700px) {
  .navigation-block__item:not(:nth-child(5n)) {
    margin: 0 0 10px 0;
  }
}
@media (max-width: 1400px) {
  .navigation-block__item:not(:nth-child(5n)) {
    margin: 0;
  }
}
@media (max-width: 1400px) {
  .navigation-block__item_position_header {
    font-size: 16px;
  }
}
.navigation-block__item_position_header:not(:nth-child(5n)) {
  margin: 0 0 20px 0;
}
@media (max-width: 1700px) {
  .navigation-block__item_position_header:not(:nth-child(5n)) {
    margin: 0 0 13px 0;
  }
}
@media (max-width: 1400px) {
  .navigation-block__item_position_header:not(:nth-child(5n)) {
    margin: 0 0 13px 0;
  }
}
@media (max-width: 900px) {
  .navigation-block__item_position_header:not(:nth-child(5n)) {
    margin: 0;
  }
}
.navigation-block__link {
  transition: opacity 0.2s ease;
}
.navigation-block__link:hover {
  opacity: 0.7;
}

.promo {
  padding: 63px 0 0 0;
  margin: 0 0 50px 0;
}
@media (max-width: 1400px) {
  .promo {
    padding: 30px 0 0 0;
    margin: 0 0 45px 0;
  }
}
@media (max-width: 900px) {
  .promo {
    padding: 25px 0 0 0;
    margin: 0 0 30px 0;
  }
}
@media (max-width: 640px) {
  .promo {
    padding: 20px 0 0 0;
    margin: 0 0 35px 0;
  }
}
.promo__list {
  display: grid;
  grid-template-rows: 480px 550px 550px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "block-one block-one block-one" "block-two block-three block-four" "block-five block-five block-six";
  gap: 60px;
}
@media (max-width: 1400px) {
  .promo__list {
    grid-template-rows: 314px 458px 458px 418px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "block-one block-one" "block-two block-three" "block-four block-six" "block-five block-five";
    gap: 24px;
  }
}
@media (max-width: 900px) {
  .promo__list {
    grid-template-rows: 280px repeat(8, 60px) 337px;
    grid-template-columns: repeat(9, 1fr);
    gap: 20px;
    grid-template-areas: none;
  }
}
@media (max-width: 640px) {
  .promo__list {
