.recommendations {
  margin: 0 0 100px 0;

  @include up-mid-size {
    margin: 0 0 50px 0;
  }

  @include low-mid-size {
    margin: 0 0 15px 0;
  }

  h2 {
    margin: 0 0 30px 0;

    @include up-mid-size {
      font-size: 30px;
      line-height: 130%;
    }

    @include min-size {
      font-size: 20px;
      margin: 0 0 25px 0;
    }
  }
}

.recommendations .products-block__item:nth-child(n+5) {
  display: none;

  @include up-mid-size {
    display: block;
  }
}